/*For Laptop (1280px)*/
@media(max-width:1350px) {
  .carousel .item{
    h3{
      font-size:17px;
      height:90px;
    }
  }
  .inbox-center{
    a{
      width:400px;
    }
  }
}

/*Small Desktop*/
@media(min-width:1024px) {
  .app-search .form-control:focus{
    width: 300px;
  }

}

/*Ipad*/
@media(min-width:768px) {

  #page-wrapper {
    position: inherit;
    margin: 0px 0 0px 220px;
  }
  .navbar-default{
    position:relative;
    width:100%;
    top:0px;
  }
  .fix-header{
    .navbar-static-top{
      position:fixed;
    }
    #page-wrapper {
      margin-top:60px;
    }
  }
  .sidebar {
    z-index: 10;
    position: absolute;
    width: 220px;
    padding-top:60px;
    height:100%;

  }

  /*If body has fix sidebar class*/
  .fix-sidebar{
    .sidebar {
      position:fixed;
      overflow:hidden;

    }
    .top-left-part{
      position:fixed;
      width:220px;

    }
    .navbar-left{
      margin-left:220px;
    }

  }
  .footer{
    left:220px;
  }

  /*If body has content-wrapper*/
  .content-wrapper {
    #page-wrapper {
      margin-left:60px;
    }
    .navbar-left{
      margin-left:0px;
    }
    .footer{left:60px;}
    .sidebar{
      width:60px;

      .hide-menu{
        display:none;
        width:180px;
        left:60px;
      }
      .sidebar-nav{
        position:absolute;
        overflow:hidden;
        .nav-second-level{
          position:absolute;
          z-index:999999;
        }
      }
      .nav-small-cap, li span span{
        display:none;
      }

      #side-menu > li:hover{
        width:300px;
        background:@extralight;

      }
      li:hover .hide-menu{
        display:inline;
      }
      #side-menu > li > a{
        padding:15px 17px 15px 20px;
      }


      li:hover .nav-second-level.in, li:hover .nav-second-level, li:hover .nav-second-level.collapse li{
        display:block;

      }
      .nav-second-level{
        position: absolute;
        left: 60px;
        background: @extralight;
        width: 240px;
        opacity:1;
        padding-bottom:20px;
        display:none;
        li{
          background:@extralight;
        }
      }
    }
    .top-left-part{
      width:60px;

    }
  }
  .navbar-top-links .dropdown-messages,
  .navbar-top-links .dropdown-tasks,
  .navbar-top-links .dropdown-alerts {
    margin-left: auto;
  }
  .mail_listing{border-left:1px solid rgba(120, 130, 140, 0.13); padding-left:20px;}
  .inbox-panel{ padding-right:20px; }

  .top-minus{
    margin-top:-62px;
    float:right;
  }
  .fix-sidebar.content-wrapper, .content-wrapper.fix-sidebar{
    .navbar-left{
      margin-left:60px!important;
    }
    .sidebar{
      position:fixed;
      .sidebar-nav{
        position:absolute;
        .nav-second-level{
          position:absolute;
          z-index:99999;
        }
      }
    }
  }

}

@media (max-width:1024px) {
  .b-r-none{ border-right:0px;}
  .carousel-inner h3{ height:90px; overflow:hidden;}
  .inbox-center{
    a{
      width:300px;
    }
  }
}
/*Phone*/
@media(max-width:767px) {
  .navbar-top-links {
    display:inline-block;
    .profile-pic img{
      margin-right:0px;
    }
  }
  .top-left-part{ width:60px;}
  .navbar-top-links li:last-child{ margin-right:0px;}
  .navbar-top-links .dropdown-messages, .navbar-top-links .dropdown-tasks, .navbar-top-links .dropdown-alerts{
    width:260px;

  }
  .row-in-br{ border-right:0px; border-bottom:1px solid @border;}
  .bg-title{
    .breadcrumb{
      float:left;
      margin-top:0px;
      margin-bottom:10px;
    }
  }
  /*Timeline*/
  ul.timeline:before {
    left: 40px;
  }

  ul.timeline > li > .timeline-panel {
    width: ~"calc(100% - 90px)";
    width: ~"calc(100% - 90px)";
    width: ~"calc(100% - 90px)";
  }

  ul.timeline > li > .timeline-badge {
    top: 16px;
    left: 15px;
    margin-left: 0;
  }

  ul.timeline > li > .timeline-panel {
    float: right;
  }

  ul.timeline > li > .timeline-panel:before {
    right: auto;
    left: -15px;
    border-right-width: 15px;
    border-left-width: 0;
  }

  ul.timeline > li > .timeline-panel:after {
    right: auto;
    left: -14px;
    border-right-width: 14px;
    border-left-width: 0;
  }
  .wizard-steps > li{
    display:block;
  }
  .dropdown .mailbox, .dropdown .dropdown-tasks {
    left:-100px;
  }

  /***** Start Update 1.5 *****/
  .fix-header{
    .navbar-static-top{
      position:fixed;
      top:0px;
      width:100%;
    }
    #page-wrapper {
      margin-top:60px;
    }
    .sidebar{
      position:fixed;

      height:350px;
      top:60px;
      z-index:100;
      overflow:auto!important;
      box-shadow:0 10px 35px rgba(0, 0, 0, 0.2);
    }
  }
  .mega-dropdown-menu{
    height:340px;
    overflow:auto;
  }
  .left-aside{
    position:relative;
    width:100%;
    border:0px;
  }
  .right-aside{
    margin-left:0px;
  }
  .chat-main-box .chat-left-aside{
    left:-250px;
    transition:0.5s ease-in;
    background:@white;
    &.open-pnl{
      left:0px;
    }
    .open-panel{
      display:block;
    }
  }
  .chat-main-box .chat-right-aside{
    margin:0px;
  }
  /***** Close Update 1.5 *****/
  .table-responsive.pro-rd {
    border: none;
  }
  .step-register, .login-register,#msform fieldset{
    position: relative;
  }
}

@media(max-width:480px) {
  .vtabs{
    .tabs-vertical{
      width:auto;
    }
  }
  .stat-item{padding-right:0px;}
  .login-box{
    width:100%;
  }
  .pro-content .pro-list-details {
    height: 100px;
    border-right: none;
  }
  .pro-list-info ul.pro-info li {
    padding: 10px 0 10px 0;
  }
  .pro-list-info ul.pro-info {
    padding-left: 0;
  }
  .pro-agent .agent-img {
    padding-top: 3px;
  }
  .pro-agent .agent-name {
    padding: 2px 0 10px 15px;
  }
}